import image4 from '../images/svg/ava04.svg';
import image5 from '../images/svg/ava09.svg';
import image6 from '../images/svg/ava01.svg';

gsap.registerPlugin(ScrollTrigger, ScrollToPlugin, TextPlugin);

ScrollTrigger.defaults({
  toggleActions: 'restart pause resume pause',
});

gsap.utils.toArray('.nav-link a').forEach(function(a) {
  a.addEventListener('click', function(e) {
    e.preventDefault();
    gsap.to(window, { duration: 1, scrollTo: e.target.getAttribute('href') });
  });
});

const test3 = document.getElementById('hover');

gsap
  .timeline()
  .from('.logo', { opacity: 0, x: -450, y: -450, rotation: 5, duration: 0.5, ease: 'power1' })
  .from('#myDIV li a', {
    opacity: 0,
    x: 450,
    y: -450,
    rotation: 5,
    stagger: { each: 0.3, ease: 'power1' },
    duration: 0.5,
    ease: 'back',
  })
  .from('#gallery', { opacity: 0, duration: 0.1 })
  .from('.demo', { duration: 0.1, opacity: 0, y: 150 })
  .from('.time1 .box', {
    opacity: 0,
    y: 160,
    rotation: 0,
    stagger: { each: 0.1, ease: 'power2.inOut' },
    duration: 1,
  })

  .from('#time5', { opacity: 0, x: -450, rotation: 5, duration: 0.2, ease: 'back' })
  .from('#time5 span', {
    opacity: 0,
    y: 160,
    rotation: 45,
    stagger: { each: 0.1, from: 'random', ease: 'bounce' },
    duration: 0.2,
    ease: 'bounce',
  })

  .set(test3, { attr: { src: image4 }, delay: 1.3 })
  .to(test3, { rotation: 5, duration: 1, ease: 'back' })
  .set(test3, { attr: { src: image5, duration: 1, delay: 1 } })
  .to(test3, { rotation: 0, duration: 1, ease: 'back' })
  .set(test3, { attr: { src: image6, duration: 1, ease: 'back' } });
