// // --- about---

const about = gsap.timeline({
  scrollTrigger: {
    trigger: '.about',
    // scrub: 3,
    pin: true,
    start: 'top +=50',
    end: '+=60%',
    toggleActions: 'play none none reverse',

    // markers: true,
  },
});

about.from('#gallery1', { opacity: 0, duration: 0.1 }).from('.time2 .box', {
  opacity: 0,
  y: 260,
  rotation: 0,
  stagger: { each: 0.1, ease: 'back.inOut(1.7)' },
  duration: 0.3,
});

gsap.utils.toArray('.about2').forEach(section => {
  const elems = section.querySelectorAll('.best');

  // Set things up
  gsap.set(elems, { y: 50, opacity: 0 });

  ScrollTrigger.create({
    trigger: section,
    start: 'top +=50',
    end: '+=60%',
    pin: true,
    onEnter: () =>
      gsap.to(elems, {
        y: 0,
        opacity: 1,
        duration: 0.5,
        stagger: 0.1,
        delay: 0.2,
        ease: 'back.inOut(1.7)',
        overwrite: 'auto',
      }),
    onLeaveBack: () =>
      gsap.to(elems, {
        y: 50,
        opacity: 0,
        duration: 0.5,
        stagger: 0.1,
        delay: 0.2,
        ease: 'back.inOut(1.7)',
        overwrite: 'auto',
      }),
  });
});

