const imgs = gsap.utils.toArray('.motiv');

const next = 5; // time to change

function crossfade() {
  gsap
    .timeline()
    .to(imgs, { y: '-=50', duration: 1 })
    .to(imgs[0], { y: '+=150', duration: 0 }); // the first to the end

  imgs.push(imgs.shift()); // the first (shift) to the end (push) from the array
  console.log(imgs);

  // start endless run
  gsap.delayedCall(next, crossfade);
}

// start the crossfade after next = 3 sec
gsap.delayedCall(next, crossfade);
